/*  SERVICE => 1260 < Screen  */
/* SERVICE Container */
.servicecontainer {
  position: relative;
}
/* SERVICE TITLE */
.servicetitle {
  position: relative;
  top: 87px;
  height: 300px;
}

@media screen and (max-width: 1260px) {
  .servicetitle {
    top: 67px;
  }
  .banner_padding {
    padding-top: 67px;
  }
}
@media screen and (max-width: 320px) {
  .servicetitle {
    top: 40px;
  }
}

/* SERVICE CONTENTS */
.servicecontents {
  /* position: relative; */
  top: 120px;
  max-width: 1280px;
  margin: auto;
  text-align: center;
}
.contentspadding {
  padding-left: 36px;
  padding-right: 36px;
}
.contentsmargin {
  position: relative;
  padding-left: 48px;
  padding-right: 48px;
}

.introtxt {
  margin-top: 64px;
  margin-bottom: 64px;
}

.flatformtitle {
  display: flex;
  align-items: center;
  justify-items: center;
  justify-content: space-between;
}

.flatformnum {
  align-items: center;
  padding: 4px 12px;
  margin: 0 16px 0 0;
  width: 100px;
  height: 28px;
  background: #f37f31;
  border-radius: 43px;
}
.explanation {
  text-align: start;
  line-height: 146%;
}
.lineheight160 {
  line-height: 160%;
}

.application {
  align-items: center;
}

/* HEALIARY */
.healiary {
  text-align: center;
}

.image-fit {
  max-width: 100%;
  max-height: 100%;
}

/*  SERVICE => 1024 < Screen < 1260 */
@media screen and (max-width: 1260px) {
}

/*  SERVICE => 768 < Screen < 1024 */
@media screen and (max-width: 1024px) {
  /* Draft design */
  .flex-row {
    flex-direction: column-reverse;
  }
  .flex-row-reverse {
    flex-direction: column-reverse;
  }
  .gap-120 {
    gap: 48px;
  }
  .gap-56 {
    gap: 48px;
  }
  .gap-40 {
    gap: 48px;
  }
  .healiary {
    margin-top: 100px;
    margin-bottom: 100px;
  }
}

/*  SERVICE => 600 < Screen < 768 */
@media screen and (max-width: 768px) {
}

/*  SERVICE => 320 < Screen < 600 */
@media (min-width: 320px) and (max-width: 600px) {
  .flatformnum {
    margin: 0 5px 0 0;
    /* padding: 5px 10px; */
    /* padding: 7px 12px; */
    width: 50%;
  }
  .flatformtitle {
    flex-direction: column;
    align-items: baseline;
    justify-content: left;
    gap: 16px;
  }
  .flatformtitle > span {
    text-align: left;
  }
  .contentspadding {
    padding-left: 12px;
    padding-right: 12px;
  }
  .contentsmargin {
    padding-left: 12px;
    padding-right: 12px;
  }
}
@media screen and (max-width: 320px) {
  /* .flatformtitle {
    justify-content: space-between;
  } */
  .flatformtitle > span {
    text-align: left;
  }
}
